import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,MenuController,Events , Platform, ToastController} from 'ionic-angular';
import { HttpServicesProvider } from '../../../providers/http-services';
import { Utils } from "../../../providers/Utils";
import { PhoneDeviceProvider } from '../../../providers/phone-device';
import { StorageProvider } from '../../../providers/storage';
import { FileTransfer,FileTransferObject } from '@ionic-native/file-transfer';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { FileOpener } from '@ionic-native/file-opener';
import { File } from '@ionic-native/file';
import { ConfigProvider } from '../../../providers/config';
import { LoadingController, Loading } from 'ionic-angular';
declare var $:any 
declare var fuzzySearch:any 
/**
 * Generated class for the PaperinfosearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-paperinfosearch',
  templateUrl: 'paperinfosearch.html',
})
export class PaperinfosearchPage {
  public isshowNode=true;
  public nodeList = [];
  public timeout=null;
  public topNode = '';
  public event: any;
  public filepath='';

  public treeurl = '';
  public list=[];//列表
  public dataList = []; //缓存页码数据（上拉加载）
	public mCurrentPage = 0;//默认page
  public where='';//搜索
  public stops='';//无数据加载完毕
  public id = '';
  public records = '0';
  public description = '';
  public fuzzysearch = '';
  public specailArray = [];
  public selectSpecail = '';
  // public assetid = 
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public menuCtrl: MenuController,
    public httpService: HttpServicesProvider,
    public phonedevice:PhoneDeviceProvider,
    public events: Events,
    public storage: StorageProvider,
    public platform:Platform,
    public toastCtrl:ToastController,
    public transfer: FileTransfer,
    public permission:AndroidPermissions,
    public fileopen:FileOpener,
    public file:File,
    public loadingCtrl: LoadingController) {
      this.phonedevice.phoneBacktoPop(this.navCtrl);

  }
  ionViewDidLoad() {
    
    this.id = this.navParams.get('id');
    console.log(this.id)
    // this.id = 'Sw1w1w10w7w1';
    if(this.id != undefined && this.id != ''){
      this.list=[];
      this.initassetlist(1,'','');

    }else{
      this.initlist(1,'','');
      this.initTree() ;
      this.getDict();
    }
    
    

    // console.log('ionViewDidLoad PaperinfosearchPage');
  }
  getDict() {//请求数据字典初始化
    var that = this;
    that.httpService.getDict("BKC_SPECAIL", data => {//
      if (data !== "") {
        this.specailArray = data;
        // that.htRegistrationType = data;
      }
    })
    
  }
  switchType(e,type){
    this.list = [];
    let obj;
    if(this.selectSpecail != ''){
        obj={"toint_fromdomain_equal":this.selectSpecail};
    }
    var where = encodeURIComponent(JSON.stringify(obj));
    this.where = where;
    this.initlist(1,this.where,this.treeurl)
  }
  finddescription(){
    this.search();
  }
  search(){
    if(this.description != ''){
      var fuzzysearch =   encodeURIComponent(this.description);
      this.fuzzysearch = fuzzysearch;
      
        // this.initdata(fuzzysearch)
      }else{
        this.fuzzysearch = '';
      }
      this.list = [];
      this.initlist(1,this.where,this.treeurl)

  }
  initassetlist(page,where,Url){
  var url = '/paperInfoAssetline/getAssetlineByLeAsset?assetid='+this.id+'&sort=papercode&order=desc&_search=false&rows=15&page='+page+'&sidx=&sord=asc';
  var that = this;
  var lists = this.list;
  that.httpService.get(url, null, (result) => {
      if(page > result.total){
      
        this.stops = 'true';
        return false;
      }else{
        this.stops = '';
      }
      this.records = result.records;
      console.log(result)
    if (result != null && result.rows.length > 0) {
      var rows = result.rows;
      for(let i=0;i<rows.length;i++){
        rows[i].papertype =  rows[i].papertype.toLowerCase().replace(".","");
        rows[i].downUrl = '/paperInfo/download/download/'+rows[i].paperid
        
      }
        this.dataList = rows;
        lists = lists.concat( this.dataList);
        this.list = lists;
        this.mCurrentPage = page;
        
    }
  });
  }
    //树初始化
    initTree() {
      var that  = this;
      $('#myzTree').empty();//清空树区域
      var setting = {
            data: {
                simpleData: {
                    enable: true,
                }
            },
            view: {
              dblClickExpand: false     
            },
            async: {
              enable:true,
              url:Utils.dealUrl("/paperCatalogue/findCatalogueTree?search=true"),
              type:'get',
              autoParam:["id"],
              dataFilter: this.datafilter
          },
            callback: {
              onClick: function(event, treeId, treeNode){
                var url = '';
                if (treeNode.id == '0') {
                  url = '&catalogueid=&devicecode=';
                }else{
                  url = '&catalogueid='+treeNode.id+'&devicecode=';
                }
                that.list = [];
                that.initlist(1,'',url)
                that.treeurl = url;
                that.menuCtrl.close();
            }
            }
        };
        this.httpService.get('/paperCatalogue/findCatalogueTree?search=true',null,(result)=>{
          $.fn.zTree.init($("#myzTree"), setting, result.data);
          fuzzySearch('myzTree','#search',null,true);
        })
    }
    datafilter(treeId, parentNode, childNodes) {
      return childNodes.data;
  }

  collection(){//收藏
    this.navCtrl.push('CollectionfilePage')
  }
//打开侧边栏

openMenu() {
  this.menuCtrl.enable(true,'combo');
  // this.menuCtrl.enable(false,'comboTree');
  this.menuCtrl.open();
  let appEl = <HTMLElement>(document.getElementsByTagName('ION-APP')[0]);
    let appElHeight = appEl.clientHeight -66;
  $('#myzTree').css({'height':appElHeight + 'px'})
}
initlist(page,where,Url){//初始化列表
  let userinfo = this.storage.get(StorageProvider.ME_USERINFO);//读取登录人的储存信息
// console.log(userinfo)
  var url = '/paperInfo/searchList?sort=papercode&order=desc&userId='+userinfo.id+'&deptId='+userinfo.deptId+'&rows=15&page='+page+'&sidx=&sord=asc&where='+where+'&fuzzysearch='+this.fuzzysearch+Url;
  var that = this;
  var lists = this.list;
  that.httpService.get(url, null, (result) => {
    // console.log(page)
    // console.log(result.total)
      if(page > result.total){
        this.stops = 'true';
        return false;
      }else{
        this.stops = '';
      }
      this.records = result.records;
      // console.log(result)
    if (result != null && result.rows.length > 0) {
      var rows = result.rows;
      for(let i=0;i<rows.length;i++){
        rows[i].papertype =  rows[i].papertype.toLowerCase().replace(".","")
        rows[i].downUrl = '/paperInfo/download/download/'+rows[i].paperid
      }
        that.dataList = rows;
        lists = lists.concat( that.dataList);
        that.list = lists;
        that.mCurrentPage = page;
    }
  });
}
checkPermissions() {
  this.permission.requestPermission(this.permission.PERMISSION.READ_EXTERNAL_STORAGE).then(res => { console.log(res) });
  this.permission.requestPermission(this.permission.PERMISSION.WRITE_EXTERNAL_STORAGE).then(res => { console.log(res) });
}
openFile(item){
  console.log(item)
  
  item['url'] =ConfigProvider.apiUrl+ item['downUrl']
  // item['url'] =Utils.dealUrl('/attach/download/paperinfo/073b393ffd2c4bf096df6d57c5ca1448')
  // "/bkc/attach/download/paperinfo/073b393ffd2c4bf096df6d57c5ca1448"
  // console.log(item['url'])
  if(item.papertype == 'gif'  || item.papertype == 'jpg' || item.papertype == 'jpeg' || item.papertype == 'png'|| item.papertype == 'bmp' )
  {
    this.phonedevice.showBigimage(item['url']);
  }else{
    let loading = this.loadingCtrl.create({
      content: '下载中，请稍等...',//数据加载中显示
      enableBackdropDismiss:true,
  });
      loading.present();
      let now = 0;  
      var url = item['url'];
      let name = item['papername'];
      console.log(url);
      if(this.platform.is('ios')){
        this.filepath=this.file.dataDirectory+item['url'];
      }else{  
        // console.log(item['papername'].substr(0,1))
        if(item['papername'].substr(0,1) == '#'){
          name = name.replace('#', ' ')
        }
        // this.file.externalApplicationStorageDirectory
        if(name.lastIndexOf('.') != -1){
          this.filepath = this.file.externalApplicationStorageDirectory + name;
        }else{
          this.filepath = this.file.externalApplicationStorageDirectory + name+'.'+item['papertype'];
        }
      }
      let filetransfer: FileTransferObject = this.transfer.create();
      filetransfer.onProgress((progressEvent) => {
        if (progressEvent.lengthComputable) {
          now = progressEvent.loaded / progressEvent.total * 100;
        }
      })
      filetransfer.download(url, this.filepath, true).then((res) => {
        this.checkPermissions();
        loading.dismiss();
        this.fileopen.open(this.filepath, this.httpService.getFileMimeType(item['papertype'])).then((data) => {
          
        }, (err) => {
          // loading.dismiss();
          this.toastCtrl.create({
            position: 'middle',
            message: '文件打开失败/无'+ this.httpService.getFileMimeType(item['papertype'])+'查看器',
            duration: 2000
          }).present();
        })
      }, (err) => {
        loading.dismiss();
        this.toastCtrl.create({
          position: 'middle',
          message: '文件下载失败',
          duration: 2000
        }).present();
      })
  }
}
doRefresh(refresher) {//下拉刷新
  this.list =[];
  if(this.id != undefined && this.id != ''){
    this.initassetlist(1,'','');

  }else{
    this.initlist(1,this.where,this.treeurl)

  }
  setTimeout(()=>{
    refresher.complete(); //停止下拉刷新
    },2000)
  }
  doInfinite(infiniteScroll){//上拉加载
    if(this.id != undefined && this.id != ''){
       this.initassetlist(this.mCurrentPage + 1 ,this.where,'')
  
    }else{
        this.initlist(this.mCurrentPage + 1 ,this.where,this.treeurl)
    }
      infiniteScroll.complete();//停止上拉加载
      if(this.stops == 'true'){
        infiniteScroll.enable(false);//停止上拉加载
      }
      // infiniteScroll.enable(false);//停止上拉加载
  }
}



